import uCharts from '@/components/u-charts/u-charts.js';
var _self;
var canvaColumn=null;
var canvaPie=null;
export default {
	data() {
		return {
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			serverData:'',
			dayData: [],
			findtimemore: "",
			findclickmore: "",
			url:""		
		}
	},
	onLoad(options) {
		this.url=this.$consturl;
		_self = this;
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData(options.deviceid);
	},
	methods: {
		butt: function() {
	      console.log("按钮已触发");
	    },
	    sub: function() {
	      const url = "../historyzhou/main";
	      uni.navigateTo({ url });
	    },
	    fh: function() {
	      // console.log(123);
	      const url = "../history/main";
	      uni.navigateTo({ url });
	    },	
		getServerData(deviceid){
			let that = this,pData,lData;
			uni.request({
			      url: that.url+'web/api/findHistoryReportDay',
			      header: {
			        'content-type': 'application/json'
			      },
			      data: {deviceid:deviceid},
			      method: 'get', // OPTIONS, GET, HEAD, POST, PUT, DELETE, TRACE, CONNECT
			      // header: {}, // 设置请求的 header
			      success: function(res){
			        that.dayData=res.data;
			         that.findtimemore=res.data.findTypesp.findtimemore;
			        that.findclickmore=res.data.findTypesp.findclickmore;
			        let Column={categories:[],series:[]};
					let Pie={series:[]};
					let asdasda={"name": "","data":""};
			        let lsg= that.dayData.findTypesp.type[0];
			        lData=that.dayData.findTypesp.listTime;
			        let sss=[];
			        for(let t in lsg){
			          sss.push(lsg[t]);
			        };
			       let x= [{ data: 0, name: "视频" },
			            { data: 1, name: "购物" },
			            { data: 2, name: "学习" },{ data: 2, name: "学习" }
			            ];
						
			            for(var i =0;i<x.length;i++){
							
			              x[i].data=sss[i];
			              // console.log(x[i].value);
			               pData=x;
			            };
					Column.categories=[ "2","4","8","10","12","14","16","18","20","22","24"];
					asdasda.data=lData;
					asdasda.name="所用时间";
					Column.series.push(asdasda);
					console.log(Column);
					_self.showColumn("canvasColumn",Column);
					Pie.series=pData;
					_self.showPie("canvasPie",Pie);
			      },
			      fail: function() {
			        // fail
			        console.log('history请求资源......fail');
			      },
			      complete: function() {
			      }
			    });
		},
		showColumn(canvasId,chartData){
			canvaColumn=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'column',
				legend:{show:true},
				fontSize:11,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				animation: true,
				categories: chartData.categories,
				series: chartData.series,
				xAxis: {
					disableGrid:true,
				},
				yAxis: {
					//disabled:true
				},
				dataLabel: true,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					column: {
						type:'group',
						width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length
					}
				  }
			});
			
		},
		touchColumn(e){
			canvaColumn.showToolTip(e, {
				format: function (item, category) {
					if(typeof item.data === 'object'){
						return category + ' ' + item.name + ':' + item.data.value 
					}else{
						return category + ' ' + item.name + ':' + item.data 
					}
				}
			});
		},
		showPie(canvasId,chartData){
			canvaPie=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'pie',
				fontSize:11,
				legend:{
				  show:true,
				  position:'right',
				  float:'center',
				  itemGap:10,
				  padding:5,
				  lineHeight:26,
				  margin:5,
				  borderWidth :1
				},
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				series: chartData.series,
				animation: true,
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				dataLabel: true,
				extra: {
					pie: {
					  labelWidth:15
					}
				},
			});
			this.piearr=canvaPie.opts.series;
		},
		touchPie(e){
			canvaPie.showToolTip(e, {
				format: function (item) {
					return item.name + ':' + item.data 
				}
			});
		},
	}
}
